/***** Basics *****/
import React from "react";
import ReactDOM from "react-dom";

/***** Libs & Frameworks *****/
import "bootstrap/dist/css/bootstrap.css";

/***** App *****/
import "./index.css";
import App from "./App";

ReactDOM.render(
  <React.StrictMode>
    <div className="bg"></div>
    <App />
  </React.StrictMode>,
  document.getElementById("root")
);

/***** Themes Scripts *****/
let themeOptions = document.querySelectorAll("label");
let optionsArray = [themeOptions[0], themeOptions[1], themeOptions[2]];
let body = document.body;
let bg = document.querySelector(".bg");
let calcScreen = document.querySelector(".screen");
let screenText = document.querySelector(".screen div");
let calcBtnsContainer = document.querySelector(".btns");
let calcBtns = document.querySelectorAll(".btns button");
let calcResetBtn = document.querySelector(".reset-btn");
let calcEqualBtn = document.querySelector(".equal-btn");
let clacDelBtn = document.querySelector(".del-btn");
let calcLogo = document.querySelector(".logo");
let calcThemesBtns = document.querySelector(".themes-btns > span");
let clacThemesContainer = document.querySelector(".choices");
let clacThemesNoneActiveBtns = document.querySelectorAll(".choices span");
let metaThemeColor = document.querySelector('meta[name="theme-color"]');

// Default Theme
themeOptions[0].onclick = function () {
  setTimeout(() => {
    let r = optionsArray.filter((o) => o !== this);

    for (let num of clacThemesNoneActiveBtns) {
      num.style.color = "#fff";
    }

    for (let label of r) {
      label.style.backgroundColor = "#252d44";
    }

    for (let btn of calcBtns) {
      btn.style.color = "#424653";
      btn.style.backgroundColor = "#eae3db";
      btn.style.boxShadow = "0 4px #b3a59a";
    }

    if (screenText.textContent !== "Math Error :(") {
      calcScreen.style.backgroundColor = "#181f32";
      screenText.style.color = "#fffffd";
    } else {
      calcScreen.style.backgroundColor = "#321818ad";
      screenText.style.color = "red";
    }

    optionsArray[1].removeAttribute("value");
    optionsArray[2].removeAttribute("value");
    this.setAttribute("value", "one");

    metaThemeColor.setAttribute("content", "#3b4664");
    clacThemesContainer.style.backgroundColor = "#252d44";
    this.style.backgroundColor = "#d43c37";
    body.style.backgroundColor = "#3b4664";
    bg.style.backgroundColor = "#3b4664";
    calcLogo.style.color = "#fffffd";
    calcThemesBtns.style.color = "#fffffd";
    calcBtnsContainer.style.backgroundColor = "#252d44";
    calcResetBtn.style.backgroundColor = "#647299";
    calcResetBtn.style.color = "#fff";
    calcResetBtn.style.boxShadow = "0 4px #424e73";
    clacDelBtn.style.backgroundColor = "#647299";
    clacDelBtn.style.color = "#fff";
    clacDelBtn.style.boxShadow = "0 4px #424e73";
    calcEqualBtn.style.backgroundColor = "#d13f30";
    calcEqualBtn.style.boxShadow = "0 4px #912418";
    calcEqualBtn.style.color = "#fff";
  }, 200);
};

// Light Theme
themeOptions[1].onclick = function () {
  setTimeout(() => {
    let r = optionsArray.filter((o) => o !== this);

    for (let num of clacThemesNoneActiveBtns) {
      num.style.color = "#36362e";
    }

    for (let label of r) {
      label.style.backgroundColor = "#d3cdce";
    }

    for (let btn of calcBtns) {
      btn.style.color = "#36362e";
      btn.style.backgroundColor = "#e5e4e0";
      btn.style.boxShadow = "0 4px #a59d94";
    }

    if (screenText.textContent !== "Math Error :(") {
      calcScreen.style.backgroundColor = "#eeeeee";
      screenText.style.color = "#36362e";
    } else {
      calcScreen.style.backgroundColor = "#d13f3087";
      screenText.style.color = "#722020";
    }

    optionsArray[0].removeAttribute("value");
    optionsArray[2].removeAttribute("value");
    this.setAttribute("value", "two");

    metaThemeColor.setAttribute("content", "#e6e6e6");
    clacThemesContainer.style.backgroundColor = "#d3cdcd";
    this.style.backgroundColor = "#c55406";
    body.style.backgroundColor = "#e6e6e6";
    bg.style.backgroundColor = "#e6e6e6";
    calcLogo.style.color = "#36362e";
    calcThemesBtns.style.color = "#36362e";
    calcBtnsContainer.style.backgroundColor = "#d3cdcd";
    calcResetBtn.style.backgroundColor = "#388187";
    calcResetBtn.style.color = "#e8fcfd";
    calcResetBtn.style.boxShadow = "0 4px #1b5f66";
    clacDelBtn.style.backgroundColor = "#388187";
    clacDelBtn.style.color = "#e8fcfd";
    clacDelBtn.style.boxShadow = "0 4px #1b5f66";
    calcEqualBtn.style.backgroundColor = "#c85401";
    calcEqualBtn.style.boxShadow = "0 4px #843904";
    calcEqualBtn.style.color = "#fff9ea";
  }, 200);
};

// Dark Theme
themeOptions[2].onclick = function () {
  setTimeout(() => {
    let r = optionsArray.filter((o) => o !== this);

    for (let num of clacThemesNoneActiveBtns) {
      num.style.color = "#ffe53c";
    }

    for (let label of r) {
      label.style.backgroundColor = "#1e0836";
    }

    for (let btn of calcBtns) {
      btn.style.color = "#ffe53c";
      btn.style.backgroundColor = "#331b4d";
      btn.style.boxShadow = "0 4px #861c9e";
    }

    if (screenText.textContent !== "Math Error :(") {
      calcScreen.style.backgroundColor = "#1e0836";
      screenText.style.color = "#ffe53c";
    } else {
      calcScreen.style.backgroundColor = "#ff18001a";
      screenText.style.color = "#df0101";
    }

    optionsArray[0].removeAttribute("value");
    optionsArray[1].removeAttribute("value");
    this.setAttribute("value", "three");

    metaThemeColor.setAttribute("content", "#17062a");
    clacThemesContainer.style.backgroundColor = "#1e0836";
    this.style.backgroundColor = "#00dad2";
    body.style.backgroundColor = "#17062a";
    bg.style.backgroundColor = "#17062a";
    calcLogo.style.color = "#ffe53c";
    calcThemesBtns.style.color = "#ffe53c";
    calcBtnsContainer.style.backgroundColor = "#1e0836";
    calcResetBtn.style.backgroundColor = "#56077c";
    calcResetBtn.style.color = "#fff";
    calcResetBtn.style.boxShadow = "0 4px #bf16f5";
    clacDelBtn.style.backgroundColor = "#56077c";
    clacDelBtn.style.color = "#fff";
    clacDelBtn.style.boxShadow = "0 4px #bf16f5";
    calcEqualBtn.style.backgroundColor = "#00decf";
    calcEqualBtn.style.boxShadow = "0 4px #76f4ef";
    calcEqualBtn.style.color = "#00383a";
  }, 200);
};
