// Basic
import { error } from "jquery";
import React from "react";
import { Container } from "react-bootstrap";

// Frameworks & Packages
import styled from "styled-components";

const Parent = styled.div`
  max-width: 450px;
  overflow: hidden;
  position: relative;
  left: 50%;
  transform: translate(-50%);

  .screen {
    width: 100%;
    height: 90px;
    font-weight: bolder;
    outline: none;
    margin-bottom: 1.5rem;
    border-radius: 10px;
    font-size: 3em;
    padding: 0 0.5em 0 0.5em;
    user-select: none;
    text-align: end;
    background-color: #181f32;
    color: #fff;
    display: flex;
    justify-content: flex-end;
    align-items: center;

    div {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      width: 100%;
    }
  }

  .btns {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(4, minmax(55px, 1fr));
    gap: 20px;
    background-color: #252d44;
    border-radius: 10px;
    font-size: 2em;
    padding: 0.7em;

    button {
      border-radius: 7px;
      border: none;
      outline: none;
      display: flex;
      justify-content: center;
      align-items: center;
      font-weight: bold;
      box-shadow: 0 4px 0 #b2a195;
      background-color: #eae3db;
      color: #454d5e;
      transition: 0.05s;

      &:active {
        transform: translateY(4px);
        box-shadow: none;
      }
    }

    .reset-btn {
      grid-column: 1/3;
      font-size: 0.7em;
      font-weight: bold;
      padding: 0.45em 0;
      background-color: #647299;
      box-shadow: 0 4px 0 #424e74;
      color: #fff;
    }

    .equal-btn {
      grid-column: 3/5;
      font-size: 0.7em;
      font-weight: bold;
      background-color: #d13f30;
      box-shadow: 0 4px 0 #912517;
      color: #fff;
    }

    .del-btn {
      background-color: #647299;
      box-shadow: 0 4px 0 #424e74;
      color: #fff;
    }

    .del-btn,
    .division-btn,
    .multiplication-btn {
      font-size: 0.7em;
    }
  }

  @media screen and (max-width: 426px) {
    .btns {
      grid-template-columns: repeat(4, minmax(40px, 1fr));
      gap: 15px;
    }
  }
`;

// JSX
class CalcBody extends React.Component {
  state = {
    result: "",
  };

  render() {
    return (
      <Container>
        <Parent>
          <div className="screen">
            <div>{this.state.result}</div>
          </div>
          <div className="btns">
            <button onClick={this.handleCalculations}>7</button>
            <button onClick={this.handleCalculations}>8</button>
            <button onClick={this.handleCalculations}>9</button>
            <button onClick={this.handleDelBtn} className="del-btn">
              DEL
            </button>
            <button onClick={this.handleCalculations}>4</button>
            <button onClick={this.handleCalculations}>5</button>
            <button onClick={this.handleCalculations}>6</button>
            <button onClick={this.handleCalculations} className="plus-btn">
              +
            </button>
            <button onClick={this.handleCalculations}>1</button>
            <button onClick={this.handleCalculations}>2</button>
            <button onClick={this.handleCalculations}>3</button>
            <button onClick={this.handleCalculations} className="minus-btn">
              -
            </button>
            <button onClick={this.handleCalculations}>.</button>
            <button onClick={this.handleCalculations}>0</button>
            <button
              onClick={() => {
                if (this.state.result !== "Math Error :(") {
                  this.setState({ result: (this.state.result += "/") });
                }
              }}
              className="division-btn"
              style={{ fontSize: 30 }}
            >
              รท
            </button>
            <button
              onClick={() => {
                if (this.state.result !== "Math Error :(") {
                  this.setState({ result: (this.state.result += "*") });
                }
              }}
              className="multiplication-btn"
            >
              x
            </button>
            <button onClick={this.resetHandler} className="reset-btn">
              RESET
            </button>
            <button onClick={this.handleEqualBtn} className="equal-btn">
              =
            </button>
          </div>
        </Parent>
      </Container>
    );
  }

  handleCalculations = (e) => {
    if (
      this.state.result.length < 14 &&
      this.state.result !== "Math Error :("
    ) {
      this.setState({ result: (this.state.result += e.target.innerText) });
    }
  };

  handleEqualBtn = () => {
    try {
      if (this.state.result !== "") {
        this.setState({ result: String(eval(this.state.result)) });
      } else {
        throw error("error");
      }
    } catch (err) {
      this.setState({ result: "Math Error :(" });

      let calcScreen = document.querySelector(".screen");
      let screenText = document.querySelector(".screen div");
      let themeOptions = document.querySelectorAll("label");
      let optionsArray = [themeOptions[0], themeOptions[1], themeOptions[2]];

      for (let label of optionsArray) {
        if (label === optionsArray[0] && label.hasAttribute("value")) {
          calcScreen.style.backgroundColor = "#321818ad";
          screenText.style.color = "red";
          screenText.style.fontSize = "0.5em";
          screenText.style.fontFamily = "monospace";
        } else if (label === optionsArray[1] && label.hasAttribute("value")) {
          calcScreen.style.backgroundColor = "#d13f3087";
          screenText.style.color = "#722020";
          screenText.style.fontSize = "0.5em";
          screenText.style.fontFamily = "monospace";
        } else if (label === optionsArray[2] && label.hasAttribute("value")) {
          calcScreen.style.backgroundColor = "#ff18001a";
          screenText.style.color = "#df0101";
          screenText.style.fontSize = "0.5em";
          screenText.style.fontFamily = "monospace";
        } else if (label === optionsArray[1] && !label.hasAttribute("value")) {
          calcScreen.style.backgroundColor = "#321818ad";
          screenText.style.color = "red";
          screenText.style.fontSize = "0.5em";
          screenText.style.fontFamily = "monospace";
        }
      }
    }
  };

  handleDelBtn = () => {
    if (this.state.result !== "Math Error :(") {
      this.setState({
        result: this.state.result.slice(0, this.state.result.length - 1),
      });
    }
  };

  resetHandler = () => {
    this.setState({ result: "" });

    let calcScreen = document.querySelector(".screen");
    let screenText = document.querySelector(".screen div");
    let themeOptions = document.querySelectorAll("label");
    let optionsArray = [themeOptions[0], themeOptions[1], themeOptions[2]];

    for (let label of optionsArray) {
      if (
        label === optionsArray[0] &&
        screenText.textContent === "Math Error :("
      ) {
        calcScreen.style.backgroundColor = "#181f32";
        screenText.style.fontSize = "1em";
        screenText.style.fontWeight = "bold";
        screenText.style.color = "#fffffd";
        screenText.style.fontFamily = "Spartan";
      } else if (
        label === optionsArray[1] &&
        label.hasAttribute("value") &&
        screenText.textContent === "Math Error :("
      ) {
        calcScreen.style.backgroundColor = "#eeeeee";
        screenText.style.fontSize = "1em";
        screenText.style.fontWeight = "bold";
        screenText.style.color = "#36362e";
        screenText.style.fontFamily = "Spartan";
      } else if (
        label === optionsArray[2] &&
        label.hasAttribute("value") &&
        screenText.textContent === "Math Error :("
      ) {
        calcScreen.style.backgroundColor = "#1e0836";
        screenText.style.fontSize = "1em";
        screenText.style.fontWeight = "bold";
        screenText.style.color = "#ffe53c";
        screenText.style.fontFamily = "Spartan";
      }
    }
  };
}

export default CalcBody;
