// Frameworks & Packages
import styled from "styled-components";
import { Container } from "react-bootstrap";

// Styles
const Parent = styled.div`
  max-width: 450px;
  position: relative;
  left: 50%;
  transform: translate(-50%);
  margin-bottom: 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;

  .logo {
    font-size: 2em;
    font-weight: bold;
    color: #fff;
    letter-spacing: 3px;
  }

  .themes-btns {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: 10px;

    span {
      color: #fff;
      font-weight: 400;
      letter-spacing: 3px;
      font-size: 0.8em;
    }

    .choices {
      display: flex;
      gap: 5px;
      background-color: #252d44;
      justify-content: center;
      align-items: center;
      border-radius: 30px;
      padding: 0.3rem;
      position: relative;

      .o {
        position: absolute;
        top: -20px;
        left: 5px;
        font-size: 0.7rem;
        font-weight: bold;
      }

      .w {
        position: absolute;
        top: -20px;
        left: 50%;
        transform: translateX(-50%);
        font-size: 0.7rem;
        font-weight: bold;
      }

      .t {
        position: absolute;
        top: -20px;
        right: 5px;
        font-size: 0.7rem;
        font-weight: bold;
      }

      input[type="radio"] {
        display: none;
        height: 0;
        width: 0;
      }

      label {
        display: flex;
        flex: auto;
        vertical-align: middle;
        align-items: center;
        justify-content: center;
        text-align: center;
        cursor: pointer;
        background-color: #252d44;
        width: 13px;
        height: 13px;
        border-radius: 50%;
        user-select: none;
      }

      input[type="radio"]:checked + label {
        background-color: #d43c37;
      }
    }
  }
`;

// JSX
const CalcHeader = () => {
  return (
    <Container>
      <Parent>
        <span className="logo">clac</span>
        <div className="themes-btns">
          <span>THEME</span>
          <div className="choices">
            <span className="o">1</span>
            <span className="w">2</span>
            <span className="t">3</span>
            <input
              type="radio"
              name="size"
              id="one"
              value="one"
              defaultChecked
            />
            <label htmlFor="one" active="on"></label>

            <input type="radio" name="size" id="two" />
            <label htmlFor="two"></label>

            <input type="radio" name="size" id="three" />
            <label htmlFor="three" className="three"></label>
          </div>
        </div>
      </Parent>
    </Container>
  );
};

export default CalcHeader;
